
import Panel from 'vux/src/components/panel/index.vue'

import jsonp from 'jsonp'

export default {
name: 'movielist-view',
components: {
Panel
},
data () {
return {
type:'1',
list:[],
// castslist:[],
page:0,
footer: {
title: ''
}
}
},
computed: {

},
filter:{

},
methods: {
initData (data){
for(var i=0,l=data.length;i<l;i++){
var castslist = [];
for(var j=0,len=data[i]['casts'].length;j<len;j++){
castslist.push(data[i]['casts'][j]['name'])
}
this.list.push({
src:data[i]['images']['small'],
title:data[i]['title'],
desc:''+data[i]['genres'].toString() + ' | ' +castslist.toString()
})
}
},
getApiData (){
console.log(this.page)
var that = this;
jsonp('https://api.douban.com/v2/movie/top250?count=8&start='+8*that.page, null, function (err, data) {
if (err) {
console.error(err.message);
} else {
that.initData(data['subjects']);
that.page++;
}
});
}
},
created: function () {
this.$nextTick(function(){
this.getApiData();
})
},
mounted: function () {

}
}



